import React, {Component} from 'react';
import * as _ from 'underscore'
import './App.css';

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            activeHeader: false,
            activeListBasic: false,
            activeListAdd: false,
            activeListVideo: false
        }
    }

    render() {
        return (
            <div className="wrapper">
                <div className="block">
                    <div className={this.state.activeHeader ? "header active" : "header"} onClick={() => {
                        this.setState({activeHeader: !this.state.activeHeader})
                    }}>
                        <p className="header__text header__text_color">{this.props.list[this.props.value].header}</p>
                        <p className="header__text">{this.props.list[this.props.value].text}</p>
                    </div>
                    <div className="list">
                        <div className={this.state.activeListBasic ? "list__basic active" : "list__basic"}
                             onClick={() => {
                                 this.setState({activeListBasic: !this.state.activeListBasic})
                             }}>
                            <p className="list__item basic">Основной материал</p>
                            <div className="links">
                                {_.range(0, this.props.list[this.props.value].basic.length).map((value) => {
                                    return <p className="link"><span
                                        className="link__count">{value + 1}</span><a target="_blank" href={
                                        this.props.list[this.props.value].basic[value]
                                    }>{this.props.list[this.props.value].basic[value]}</a>
                                    </p>
                                })}
                            </div>
                        </div>
                        <div className={this.state.activeListAdd ? "list__additional active" : "list__additional"}
                             onClick={() => {
                                 this.setState({activeListAdd: !this.state.activeListAdd})
                             }}>
                            <p className="list__item additional">Дополнительный материал</p>
                            <div className="links">
                                {_.range(0, this.props.list[this.props.value].additional.length).map((value) => {
                                    return <p className="link"><span
                                        className="link__count">{value + 1}</span><a target="_blank" href={
                                        this.props.list[this.props.value].additional[value]
                                    }>{this.props.list[this.props.value].additional[value]}</a>
                                    </p>
                                })}
                            </div>
                        </div>
                        <div className={this.state.activeListVideo ? "list__video active" : "list__video"}
                             onClick={() => {
                                 this.setState({activeListVideo: !this.state.activeListVideo})
                             }}>
                            <p className="list__item video">Видео материал</p>
                            <div className="links">
                                {_.range(0, this.props.list[this.props.value].video.length).map((value) => {
                                    return <p className="link"><span
                                        className="link__count">{value + 1}</span><a target="_blank" href={
                                        this.props.list[this.props.value].video[value]
                                    }>{this.props.list[this.props.value].video[value]}</a>
                                    </p>
                                })}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default App;
